import Topbar from '../../components/topBar/topbarx.vue'
import LodingPage from '../../pages/loding.vue'
export default {
	data() {
		return {
			lodingShow: false, //加载
			title: '选择常用旅客',
			city: {
				from: '北京',
				to: '成都'
			},
			cyUserlist: [],
			isReading: true, // 防抖
		}
	},
	onLoad() {
		this.getData()
		
	},
	onShow() {
		this.isReading = true
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		// 新增常用旅客
		goAddvister(){
			// 防抖处理
			if(this.isReading){
				this.isReading = false
				uni.navigateTo({
					url:'../addvister/addvister'
				})
			}
			
		},
		// 进入旅客详情页面
		goSetUser(item){
			// uni.navigateTo({
			// 	url:'../setvister/setvister?id='+item.name
			// })
		},
		// 返回上级目录
		goBack(){
			uni.switchTab({
				url: '../mine/mine'
			})
		},
		getData(){
			let _this = this
			let token = this.$store.state.userInfo.token
			console.log(token)
			// uni.showLoading({
			// 	title: '加载中...'
			// })
			_this.lodingShow = true
			uni.request({
				url: this.$http + '/api/user/userPassenger',
				method: 'POST',
				data: {
					token: token
				},
				success(res) {
					console.log('常用旅客列表数据', res)
					// uni.hideLoading()
					_this.lodingShow = false
					if(res.data.code == 1){
						_this.cyUserlist = res.data.data
					}
				}
			})
		}
	},
	components: {
		Topbar,
		LodingPage
	}
}
